.shimmer-container {
  flex-wrap: wrap;
  justify-content: center;
  gap: 20px;
  padding: 20px;
  display: flex;
}

.shimmer-card {
  background: linear-gradient(to right, #f0f0f0 0%, #e0e0e0 20%, #f0f0f0 40% 100%) 0 0 / 800px 104px;
  border-radius: 12px;
  width: 250px;
  height: 300px;
  animation: 1.5s linear infinite shimmer;
}

@keyframes shimmer {
  0% {
    background-position: -800px 0;
  }

  100% {
    background-position: 800px 0;
  }
}
